.InfoPanel {
  position: absolute;
  height: 150px;
  padding: 5px;
  width: 160px;
  top: 16%;
  left: 24px;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 2;
  text-align: justify;
}

.InfoPanel-List {
  margin: 0;
  padding: 0;
  list-style-type: none;
  font-family: 'VT323', monospace;
  color: green;
  
}

.InfoPanel-List li  {
  display: -ms-flexbox;
  display: flex;
  -webkit-animation: interferenceSkew 2s infinite;
          animation: interferenceSkew 2s infinite;
  font-size: 15px;
}

.InfoPanel-List li span {
  display: block;
  padding: 0px 4px;
  width: 33%;
}


@-webkit-keyframes interferenceSkew {
  0% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg)
  }
  10% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg)
  }
  20% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg)
  }
  30% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg)
  }
  40% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg)
  }
  50% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg)
  }
  60% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg)
  }
  70% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg)
  }
  90% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg)
  }
  95% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg)
  }
  98% {
    -webkit-transform: skewX(80deg);
            transform: skewX(80deg)
  }
  100% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg)
  }

}


@keyframes interferenceSkew {
  0% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg)
  }
  10% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg)
  }
  20% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg)
  }
  30% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg)
  }
  40% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg)
  }
  50% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg)
  }
  60% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg)
  }
  70% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg)
  }
  90% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg)
  }
  95% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg)
  }
  98% {
    -webkit-transform: skewX(80deg);
            transform: skewX(80deg)
  }
  100% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg)
  }

}


