.Video {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: relative;
}

.Video-ColorFilter {
  overflow: hidden;
  -webkit-clip-path: url(#glassesSvg);
          clip-path: url(#glassesSvg);
  will-change: auto;
}

.Video-Canvas {
  overflow: hidden;
  -webkit-filter: grayscale(20%) sepia(0.1);
          filter: grayscale(20%) sepia(0.1);
  mix-blend-mode: color-burn;
  -webkit-animation: interferenceTranslate 4ms infinite;
          animation: interferenceTranslate 4ms infinite;
}

.Video-Canvas_isScaled {
  -webkit-animation: dancingAnimation 0.8s infinite;
          animation: dancingAnimation 0.8s infinite;
}
  

@-webkit-keyframes interferenceTranslate {
  0% {
    -webkit-transform: scale(1.1) translate(0px, 0px) skewX(0deg);
            transform: scale(1.1) translate(0px, 0px) skewX(0deg)
  }
  25% {
    -webkit-transform: scale(1.1) translate(-1px, -1px) skewX(-1deg);
            transform: scale(1.1) translate(-1px, -1px) skewX(-1deg)
  }
  50% {
    -webkit-transform: scale(1.1) translate(-2px, -2px) skewX(-2deg);
            transform: scale(1.1) translate(-2px, -2px) skewX(-2deg)
  }
  75% {
    -webkit-transform: scale(1.1) translate(1px, 1px) skewX(1deg);
            transform: scale(1.1) translate(1px, 1px) skewX(1deg)
  }
  100% {
    -webkit-transform: scale(1.1) translate(2px, 2px) skewX(2deg);
            transform: scale(1.1) translate(2px, 2px) skewX(2deg)
  }
}
  

@keyframes interferenceTranslate {
  0% {
    -webkit-transform: scale(1.1) translate(0px, 0px) skewX(0deg);
            transform: scale(1.1) translate(0px, 0px) skewX(0deg)
  }
  25% {
    -webkit-transform: scale(1.1) translate(-1px, -1px) skewX(-1deg);
            transform: scale(1.1) translate(-1px, -1px) skewX(-1deg)
  }
  50% {
    -webkit-transform: scale(1.1) translate(-2px, -2px) skewX(-2deg);
            transform: scale(1.1) translate(-2px, -2px) skewX(-2deg)
  }
  75% {
    -webkit-transform: scale(1.1) translate(1px, 1px) skewX(1deg);
            transform: scale(1.1) translate(1px, 1px) skewX(1deg)
  }
  100% {
    -webkit-transform: scale(1.1) translate(2px, 2px) skewX(2deg);
            transform: scale(1.1) translate(2px, 2px) skewX(2deg)
  }
}


@-webkit-keyframes dancingAnimation {
  0% {
    -webkit-transform: scale(1) translate(-10px, 0px);
            transform: scale(1) translate(-10px, 0px)
  }
  25% {
    -webkit-transform: scale(1.5) translate(-5px, -30px);
            transform: scale(1.5) translate(-5px, -30px)
  }
  50% {
    -webkit-transform: scale(1.8) translate(0px, -60px);
            transform: scale(1.8) translate(0px, -60px)
  }
  75% {
    -webkit-transform: scale(1.4) translate(5px, -90px);
            transform: scale(1.4) translate(5px, -90px)
  }
  100% {
    -webkit-transform: scale(1) translate(10px, 0px);
            transform: scale(1) translate(10px, 0px)
  }

}


@keyframes dancingAnimation {
  0% {
    -webkit-transform: scale(1) translate(-10px, 0px);
            transform: scale(1) translate(-10px, 0px)
  }
  25% {
    -webkit-transform: scale(1.5) translate(-5px, -30px);
            transform: scale(1.5) translate(-5px, -30px)
  }
  50% {
    -webkit-transform: scale(1.8) translate(0px, -60px);
            transform: scale(1.8) translate(0px, -60px)
  }
  75% {
    -webkit-transform: scale(1.4) translate(5px, -90px);
            transform: scale(1.4) translate(5px, -90px)
  }
  100% {
    -webkit-transform: scale(1) translate(10px, 0px);
            transform: scale(1) translate(10px, 0px)
  }

}



